.App {
  text-align: center;
  padding: 20px;
}
.App-header {
  background-color: white;
  padding: 20px;
  border: 2px solid black;
  margin-bottom: 40px;
}

.Segments {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.Segment {
  width: 300px;
  margin-bottom: 20px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: white;
}

.Segment h2 {
  margin-top: 0;
  background-color: black;
  color: white;
}

.Subsegments {
  list-style: none;
  padding: 0;
}

.Subsegments li {
  margin-bottom: 5px;
}

.footer {
  background-color: grey;
  color: black;
  padding: 53px;
  font-size: 23px;
}
